import { numericToCalendar } from "../../util/dateHelpers";
import { months } from "../../util/globals";
import { getVisibleDateRange, getNumSelectedTips } from "../../util/treeVisibilityHelpers";
var plurals = {
    country: "countries",
    authors: "authors"
};
export var pluralise = function(word, n) {
    if (n === 1) {
        if (word === "authors") word = "author";
    } else {
        if (word in plurals) word = plurals[word];
        if (word.slice(-1).toLowerCase() !== "s") word += "s";
    }
    word = word.replace(/_/g, " ");
    return word;
};
export var styliseDateRange = function(date) {
    var dateStr = typeof date === "number" ? numericToCalendar(date) : date;
    var fields = dateStr.split("-");
    // 2012-01-22
    if (fields.length === 3) {
        return "".concat(months[fields[1]], " ").concat(fields[0]);
    }
    // other cases like negative numbers
    return dateStr;
};
/**
 * @returns {string}
 */ export var datasetSummary = function(param) {
    var nodes = param.nodes, visibility = param.visibility, mainTreeNumTips = param.mainTreeNumTips, branchLengthsToDisplay = param.branchLengthsToDisplay, t = param.t;
    var nSelectedSamples = getNumSelectedTips(nodes, visibility);
    var sampledDateRange = getVisibleDateRange(nodes, visibility);
    var summary = ""; /* text returned from this function */ 
    /* Number of genomes & their date range */ if (branchLengthsToDisplay !== "divOnly" && nSelectedSamples > 0) {
        summary += t("Showing {{x}} of {{y}} genomes sampled between {{from}} and {{to}}", {
            x: nSelectedSamples,
            y: mainTreeNumTips,
            from: styliseDateRange(sampledDateRange[0]),
            to: styliseDateRange(sampledDateRange[1])
        });
    } else {
        summary += t("Showing {{x}} of {{y}} genomes", {
            x: nSelectedSamples,
            y: mainTreeNumTips
        });
    }
    summary += ".";
    return summary;
};
